body{
  background-image: url("./assets/summerland.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.page{
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 800px;
  max-height: 100vw;
}

.title{
  color: white;
  font-weight: 500;
  font-size: 47px;
}

.Finder{
  position: relative;
  max-width: 400px;
  background-color: #fff;
  border-radius: 4px;
  display: block;
  padding: 25px;
  margin: 10px 10px 10px 10px;
  -webkit-box-shadow: 4px 4px 16px black;
          box-shadow: 4px 4px 16px black;
}

.Booker{

}

form {
  width: 100%;
  font-size: 14px;
}

.input-wrapper{
  display: block;
  max-width:100%;
  overflow: hidden;
  margin-top: 20px;
}

.input-wrapper>input{
  background-color: #efefef;
  border-radius: 3pt;
  margin-right: 20px;
  margin-left: 2px;
  width: 100%;
  padding: 12px;
  border: none;
}

.calendar-wrapper{
  width:100%;
  margin-right:20px;
  overflow: hidden;
}

.calendar{
  overflow:visible;
  width: 50%;
  display: inline-block;
  padding-left: 0;
}

.calendar-c{
  position:absolute;
  z-index: 10;
  max-width: 250px;
}
.end{
  right: 25px;
}

.start{
  left: 28px;
}

.calendar>input:nth-child(1){
  margin: 0 !important;
}

.calendar>input{
  width:100%;
  color: text;
}
/*  React Calendar overrides*/
.react-calendar{
  border: none !important;
  -webkit-box-shadow: grey 0 1px 6px;
          box-shadow: grey 0 1px 6px;
}

.input-wrapper>span{
  display:block;
  padding: 5px;
}

.input-area{
  padding: 3px;
  margin-right: 5px;
}

.bttn{
  border: none;
  background-color: teal;
  color: white;
  margin-top: 20px;
  padding:10px;
  border-radius: 3px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: inline-block;
  position: relative;
  right: 0px;
}

.submit-button:hover{
  cursor:pointer;
}

.button-wrapper{
  width: 100%;
}

:focus{
  font-weight: bold;
  outline: none;
  color: black;
}

.parameter-tag-wrapper{
  display: block;
}

.parameter-label{
  font-size: 10px;
}

.parameter-tag{
  display: inline-block;
  border-radius: 3px;
  border: 1.5px solid teal;
  color: #323232;
  font-size: 10px;
  padding: 4px;
  margin: 5px;
}

.campsites{
  margin-left: auto;
  margin-right: auto;
}

.bookButton{
  margin: 20px;;
}
.campsite{
  background-color: lightGrey;  
  font-size: 5%;
  display:inline-block;
  margin: 10px;
  max-width: 340px;;
  min-width: 250px;
  width: 100%;
}

.campsite> *{
  margin: 10px;
}

.Loader{
  margin-left: auto;
  margin-right: auto;
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.loader-img{
  display:block;
  margin-left: auto;
  margin-right: auto;
  width: 200px;
  height:200px;
}


